/* ////////////////////////////////////////////////////////// */
/* Contact                                                   */

.contact-body{
  text-align: center;
  margin: auto;
  /* position: fixed; */
  width: 100%;
  height: 100%;
  justify-content: center;
  padding-top: 2em;
}

.contact-grid{
  text-align: center;
  justify-content: center;
  width: 80%;
  height: auto;
  background: white;
  color: black;
  border-top: 6px solid lightsalmon;
  margin-bottom: 20px;

}

.contact-grid h2{
  font-family: 'Suez One', serif;
}
.contact-grid p{
  font-family: 'Arvo', serif;
  font-weight: bold;
}
.contact-grid hr{
  border-top: 5px dotted black;
  width: 50%;
  margin:auto;
}
.contact-list{
  text-align: center;
}

.contact-list i{
   font-size: 45px;
   padding-right: 5rem;
}

@media only screen and (max-width: 1039px){
  .contact-list i{
    font-size: 35px;
    padding-right: 2rem;
 }
  .listItemContent span{
    font-size: 1.9rem;
  }
}

@media only screen and (max-width: 839px){
  .contact-list i{
    font-size: 30px;
    padding-right: 2rem;
 }
  .listItemContent span{
    font-size: 1.8rem;
  }
}

@media only screen and (max-width: 720px){
  .contact-list i{
    font-size: 30px;
    padding-right: 1.5rem;
 }
  .listItemContent span{
    font-size: 1.5rem;
  }
}

@media only screen and (max-width: 590px){
  .contact-list i{
    font-size: 25px;
    padding-right: 1rem;
 }
  .listItemContent span{
    font-size: 1rem;
  }
}
@media only screen and (max-width: 400px){
  .contact-list i{
    font-size: 20px;
    padding-right: 0.7rem;
 }
  .listItemContent span{
    font-size: 0.7rem;
  }
}


/* //////////////////////////////////////////////////////// */
/*     Resume page Css                                     */

/* Left part */

.resume-left-div{
  text-align: center;
}
.resume-avatar{
  height: 200px;
}
.resume-fullname{
  padding-top: 2em;
}
.resume-hr{
  border-top: 3px solid blue;
  width: 50%; 
}

/* Right part */

.resume-right-col{
  background: #232526;  /* fallback for old browsers */
  background: -webkit-linear-gradient(to left, #414345, #232526);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to left, #414345, #232526); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  color: white;
  padding: 2em;
  border-radius: 40px;
}

.resume-grid{
  display: grid;
  grid-template-columns: 1fr 2fr;
}
.schoolname{
  margin:0;
}

.skills-grid{
  display: grid;
  grid-template-columns: 1fr ;
}

.skill-container{
  width: 100%;
} 